* {
  margin: 0px;
  padding: 0px;
  background-color: #18122B;
  color: #635985;
}


.container {
  margin-top: 5pc;
  border: 1px solid;
  padding: 1pc;

  width: 50vw;
}

.heading {
  color: white;
  text-align: center;
}

.elements {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-content: center;
  align-items: center;
}

.search {
  border-radius: 1pc;
  width: 46vw;
  height: 8vh;
  background-color: white;
}

.card {
  width: 48vw;

  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}


.card-body {
  display: flex;
  color: white;
  margin: 2px;
}

.body-text {
 
  margin: 2px;
  width: 14.7vw;
  
  text-align: center;
  color: white;
}